import Vue from 'vue'
import Router from 'vue-router'
import Index from './index'
import Refresh from 'src/components/refresh.vue'
import Vuex from 'vuex'

Vue.use(Router)
//Vue.use(Vuex)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/refresh',
      name: 'refresh',
      component: Refresh
    }
  ]
})



