<template>
  <div>
  	<h1>标题</h1>
    <p> {{$store.state.count}} </p>
    <p><input type="button" value="提交" @click="changeCount" /></p>
  </div>
</template>

<script>
export default {
	name: 'index',
	methods: {
		changeCount () {
			this.$store.commit('CHANGE_COUNT');
		}
	}
}
</script>


